@mixin btn-destroy{
	border: none;
	background: transparent;
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	outline: none;
	cursor: pointer;
}

@mixin input-destroy{
	border: none;
	background: transparent;
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	outline: none;
}
//   
@mixin list-destroy{
	list-style: none;
	margin: 0;
	padding: 0;
}