.parallax__img--about_main{
	background: linear-gradient(65.95deg, #0F0F0F 0.6%, #000000 0.6%, rgba(0, 0, 0, 0) 42.86%), linear-gradient(0deg, rgba(0, 0, 0, 0.43), rgba(0, 0, 0, 0.43)),  url(../img/about/bg-about.jpg);
	background-blend-mode: multiply, normal, normal;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

.main__title--about{
	font-size: 30px;
}

@media(min-width: 767px){
	.main__title--about{
		font-size: 48px;
		line-height: 48px;
	}
}

@media(min-width: 1280px){
	.main__title--about{
		font-size: 64px;
		line-height: 64px;
	}
}

@media(min-width: 1680px){
	.main__title--about{
		font-size: 90px;
		line-height: 90px;
	}
}